main.sidebar_and_content {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

div.larrymg_me--grid_container--sidebar_and_content {
  display: grid;
  /* Set widths for sidebar & content divs here. */
  grid-template-columns: minmax(225px, 20%) 1fr;
  gap: 1rem;
  width: 100%;
  height: 100%;
}
